import { Component, OnInit, AfterViewInit, ViewChild } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';
import {
  BarcodeScannerOptions,
  BarcodeScanner
} from "@ionic-native/barcode-scanner/ngx";

import { BalanceServiceService } from '../../../services/balance-service.service';
import { AlertService } from '../../../services/alert.service';
import { PayDriverService } from 'src/services/pay-driver.service';
import { UserService } from 'src/services/user.service';

import { TransationsService } from '../../../services/transactions.service';

import {
  ScannerQRCodeConfig,
  ScannerQRCodeSelectedFiles,
  NgxScannerQrcodeService,
  ScannerQRCodeResult,
  NgxScannerQrcodeComponent,
} from 'ngx-scanner-qrcode';


import { delay } from 'rxjs/operators';

@Component({
  selector: 'app-passenger-home',
  templateUrl: './passenger-home.page.html',
  styleUrls: ['./passenger-home.page.scss'],
})
export class PassengerHomePage implements OnInit, AfterViewInit {

  @ViewChild('action') action: NgxScannerQrcodeComponent;

  public qrCodeResult: ScannerQRCodeSelectedFiles[] = [];
  public qrCodeResult2: ScannerQRCodeSelectedFiles[] = [];

  public config: ScannerQRCodeConfig = {
    // fps: 1000,
    vibrate: 400,
    // isBeep: true,
    // decode: 'macintosh',
    deviceActive: 0, // Camera 1 active
    constraints: {
      audio: false,
      video: {
        width: window.innerWidth,
      },
    },
  };


  private user_pk:any;
  private subscription_balance: Subscription = new Subscription();
  private subscription_payment: Subscription = new Subscription();
  private subscription_user: Subscription = new Subscription();
  private subscription_transac: Subscription = new Subscription();

  constructor(private barcodeScanner: BarcodeScanner,private router: Router,
    private balanceService: BalanceServiceService,
    private alertService: AlertService,
    private userService: UserService,
    private payDriverService: PayDriverService,
    private transacService: TransationsService,
    private qrcode: NgxScannerQrcodeService,
    route:ActivatedRoute) {
      route.params.subscribe(val => {
        // this.payDriver(1);
        this.getBalance();
        this.getTransactions();
      });

     }

  private balance:any;// = parseFloat("1955.00").toLocaleString();
  private token = '1234567890ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz';
  ngOnInit() {
    // this.decrypt_qr_data("32372e5569742147646321313221333133332130383b35373b353321464c552a3139313121295169686d687171686f64215275606f656073652155686c6428");

  }


  ngAfterViewInit(): void {
    this.action.isReady.pipe(delay(1000)).subscribe(() => {
      this.action.start();
    });
  }

  public onEvent(e: ScannerQRCodeResult[]): void {
    console.log(e, 'test2');
  }

  public handle(action: any, fn: string): void {
    action[fn]().subscribe(console.log, 'test');
  }

  public onDowload(action): void {
    action.download().subscribe(console.log, alert);
  }

  public onSelects(files: any): void {
    this.qrcode
      .loadFiles(files)
      .subscribe((res: ScannerQRCodeSelectedFiles[]) => {
        this.qrCodeResult = res;
      });
  }

  public onSelects2(files: any): void {
    this.qrcode
      .loadFilesToScan(files, this.config)
      .subscribe((res: ScannerQRCodeSelectedFiles[]) => {
        console.log(res);
        this.qrCodeResult2 = res;
      });
  }



















  scanQR(){
    this.barcodeScanner
      .scan()
      .then(barcodeData => {
      alert("Barcode data " + JSON.stringify(barcodeData));
       console.log(barcodeData);
      //  alert(barcodeData.text);
       this.decrypt_qr_data(barcodeData.text);
        this.payDriver(this.user_pk);
        this.getBalance();
        this.getTransactions();
      })
      .catch(err => {
        console.log("Error", err);
      });
  }

  payDriver(driver_Pk){
    var amountPaid=0;
    var transac_no = this.makeid(6);


    this.subscription_user.add(
      this.userService.getUserById(driver_Pk).subscribe(
        result => {
          amountPaid = result.result[0]['driver_rate'];

            this.subscription_payment.add(
              this.payDriverService.payDriver(transac_no,amountPaid,1,driver_Pk).subscribe(
                result => {
                  this.alertService.simpleAlert('success', 'Success', 'Payment successful!');
                  this.getBalance();
                },
                error => {
                  this.alertService.simpleAlert('info', 'Info', 'Please check either your Email or Password is Incorrect');
                }
              )
            );
        },
        error => {
          this.alertService.simpleAlert('info', 'Info', 'failed to fetch rate');
        }
      )
    );


  }

  topUp(){
    this.router.navigate(['/passenger-topup']);
  }

  decrypt_qr_data(data){
    var decrypt = (salt, encoded) => {
      const textToChars =  (text) => text.split('').map((c) => c.charCodeAt(0))
      const appySaltToChar = (code) => textToChars(salt).reduce((a,b) => a ^ b, code)

      return encoded
      .match(/.{1,2}/g)
      .map((hex) => parseInt(hex, 16))
      .map(appySaltToChar)
      .map((charCode) => String.fromCharCode(charCode))
      .join("")
    }

    var dec = decrypt(this.token, data);
    dec = dec.split('/');
    console.log('USER PK DRIVER' + dec[0]);
    this.user_pk =dec[0];
  }

  getBalance(){
    this.subscription_balance.add(
      this.balanceService.get_balance().subscribe(
        result => {

          var currentBal = result.result[0].balance;
          this.balance = currentBal;

        },
        error => {
          this.alertService.simpleAlert('info', 'Info', 'Please check either your Email or Password is Incorrect');
        }
      )
    );
  }


  public transactions: [];

  getTransactions(){
    this.subscription_transac.add(
      this.transacService.get_transactions(5).subscribe(
        result => {
          console.log(result);
          if(result.result.length>0){
            this.transactions = result.result;
            console.log(this.transactions);
          }
          else{
            this.transactions= [];
          }
        },
        error => {
        }
      )
    );
  }

  makeid(length) {
    var result           = '';
    var characters       = '01234567899876543210';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() *
 charactersLength));
   }
   var date = new Date()
   var d_date = date.getFullYear().toString() + ((date.getDate() + 1).toString().length < 2 ? '0'+ date.getDate().toString() : date.getDate().toString()) + ((date.getMonth() + 1).toString().length < 2 ? '0'+ date.getMonth().toString() : date.getMonth().toString());
   var hour_date = (date.getHours() + 1).toString().length < 2 ? '0'+ date.getHours().toString() : date.getHours().toString();
   var min_date = (date.getMinutes() + 1).toString().length < 2 ? '0'+ date.getMinutes().toString() : date.getMinutes().toString();
   var sec_date = (date.getSeconds() + 1).toString().length < 2 ? '0'+ date.getSeconds().toString() : date.getSeconds().toString();
  result = d_date + hour_date + min_date + sec_date+ result
   return result;
}


}
