import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { GlobalVariable } from '../server';

@Injectable({
  providedIn: 'root'
})
export class PayDriverService {


  private url = GlobalVariable.BASE_API_URL;

  constructor(private http: HttpClient) { }


  public payDriver(transaction_number: string, amount:number,transaction_type:number,transaction_recepient:number): Observable<any> {

    
    var user = localStorage.getItem('loginSession');
    var user_id = JSON.parse(user)['pk'];
    var account_type = JSON.parse(user)['account_type'];

    console.log(transaction_recepient+ 'abac');

    return this.http.post<any>(this.url.concat('api/user/pay-driver'),  {
      transaction_number: transaction_number,
     amount:amount,
      user_id:user_id,
       account_type:account_type,
     transaction_type:transaction_type,
     transaction_recipient:transaction_recepient,
     withCredentials: true
    }).pipe(
      catchError(this.handleError)
    );
  }


  private handleError(err: HttpErrorResponse) {
    let errorMessage = '';
    if (err.error instanceof ErrorEvent) {
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return throwError(errorMessage);
  }

}
